export default {
	data() {
		return {
			oldPassword: "",
			newPassword: "",
			reNewPassword: "",
		}
	},
	computed: {
		//监听输入框是否全部有内容
		disabled() {
			return this.oldPassword === "" || this.newPassword === "" || this.reNewPassword === "";
		},
		//校验两次密码是否相同
		passwordIsSame(){
			return this.newPassword === this.reNewPassword;
		}
	},
	methods: {
		submit(){
			if(!this.passwordIsSame) {
				uni.showToast({
					title: "两次密码不一致",
					icon: "none"
				})
				return
			}
			console.log('提交');
		}
	}
}
